import React, { Fragment, useCallback, useEffect, useState } from "react";
import { Link } from "gatsby";
import { Button, Card, Col, Container, Image, Row } from "react-bootstrap";
import Loading from "../../components/common/loading";
import LoadMore from "../../components/common/loadmore";
import { utils } from "../../core/helper";

const BlogsData = (data) => {
  const { siteUrl, blogImages, db } = data;
  let count = 0;
  const { numberItemsPagination, loadMoreItems, isLoading } = LoadMore(6);
  const [latestBlogs, setLatestBlogs] = useState([]);
  const [trendingBlogs, setTrendingBlogs] = useState([]);
  const [isMounted, setIsMounted] = useState(false);
  const [blogCalled, setBlogCalled] = useState(false);

  const loadBlogs = useCallback(async () => {
    const blogsArr = [];
    let propsData = data.blogs;
    propsData?.edges.sort((a, b) => b.node.keyId - a.node.keyId);
    if (propsData?.edges?.length > 0) {
      for (let blog of propsData?.edges) {
        for (let blogImgObj of blogImages) {
          const jsonBlogImgEndPath = blog?.node?.image?.split("/");
          const graphBlogImgEndPath =
            blogImgObj?.node?.gatsbyImageData?.images?.fallback?.src?.split(
              "/"
            );
          if (jsonBlogImgEndPath?.[2] === graphBlogImgEndPath?.[4]) {
            blog.node.blogImg =
              blogImgObj?.node?.gatsbyImageData?.images?.fallback?.src;
            blogsArr.push({ blog });
            setLatestBlogs(blogsArr);
          }
        }
      }
    }
  }, [data.blogs, blogImages]);

  useEffect(() => {
    if (isMounted) {
      loadBlogs();
      setBlogCalled((blogCalled) => !blogCalled);
    }
  }, [loadBlogs, isMounted]);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  useEffect(() => {
    if (isMounted) {
      if (latestBlogs.length > 0) {
        const sortTrendingBlogs = async (db, blogs) => {
          let addViewCountArr = [];
          let blogsCountsArr = await utils.getDocData(db, "viewCount");
          for (let blogObj of blogs) {
            for (let blogsCountDoc of blogsCountsArr) {
              if (blogObj?.blog?.node?.slug === blogsCountDoc?.key) {
                blogObj.blog.node.viewCount = blogsCountDoc?.count;
                addViewCountArr.push(blogObj);
              }
            }
            setTrendingBlogs(addViewCountArr);
          }
        };
        sortTrendingBlogs(db, latestBlogs);
      }
    }
  }, [isMounted, db, blogCalled, latestBlogs]);

  trendingBlogs.sort((a, b) => b.blog.node.viewCount - a.blog.node.viewCount);
  latestBlogs.sort(
    (a, b) => b.blog.node.blog_created_date - a.blog.node.blog_created_date
  );

  return (
    <Container fluid className="p-0">
      {trendingBlogs?.length > 0 && (
        <Fragment>
          <div className="blog_details_bg">
            <h1>Trending Blogs</h1>
          </div>
          <div className="blog_list_bg">
            <Container className="blog_container_position">
              <Row className="blog_row_gap">
                {trendingBlogs?.slice(0, 6).map((ele, ind) => {
                  const { blog } = ele;
                  let blogId = ind;
                  if (blogId % 2 === 0) {
                    count = count + 1;
                  }
                  return (
                    <Col lg={6} xs={12} className="p-lg-0">
                      {count % 2 !== 0 ? (
                        <Card key={ele.id} className="jt_blog_card">
                          <Row className="mx-lg-0">
                            <Col
                              sm={6}
                              xs={12}
                              className="p-sm-0 d-flex justify-content-center"
                            >
                              <Link
                                to={`${blog?.node?.slug}`}
                                className="text-decoration-none  d-block "
                              >
                                <div
                                  id="arrow_right"
                                  className="jt_blog_img_wrapper"
                                >
                                  <Image
                                    src={siteUrl + blog?.node?.blogImg}
                                    alt={blog?.node?.title}
                                    className="jt_blog_img"
                                  />
                                  <div className="blog_arrow_first">
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="15"
                                      height="18"
                                      viewBox="0 0 15 18"
                                      fill="none"
                                    >
                                      <path
                                        d="M0 9L15 0.339745L15 17.6603L0 9Z"
                                        fill="white"
                                      />
                                    </svg>
                                  </div>
                                </div>
                              </Link>
                            </Col>
                            <Col
                              sm={6}
                              xs={12}
                              className="p-sm-0 d-flex justify-content-center "
                            >
                              <Link
                                to={`${blog?.node?.slug}`}
                                className="jt_blog_card_body"
                              >
                                <p className="jt_blog_date">
                                  {blog?.node?.blog_created_date}
                                </p>

                                <p className="jt_blog_title">
                                  {blog?.node?.title}
                                </p>
                                <p className="jt_blog_description">
                                  {blog?.node?.short_description.slice(0, 65) +
                                    "..."}
                                </p>
                              </Link>
                            </Col>
                          </Row>
                        </Card>
                      ) : (
                        <Card key={ele.id} className="jt_blog_card">
                          <Row className="mx-lg-0">
                            <Col
                              sm={6}
                              xs={12}
                              className="p-sm-0 d-flex justify-content-center order_two "
                            >
                              <Link
                                to={`${blog?.node?.slug}`}
                                className="jt_blog_card_body"
                              >
                                <p className="jt_blog_date">
                                  {blog?.node?.blog_created_date}
                                </p>

                                <p className="jt_blog_title">
                                  {blog?.node?.title}
                                </p>
                                <p className="jt_blog_description">
                                  {blog?.node?.short_description.slice(0, 65) +
                                    "..."}
                                </p>
                              </Link>
                            </Col>
                            <Col
                              sm={6}
                              xs={12}
                              className="p-0 d-flex justify-content-center order_one"
                            >
                              <Link
                                to={`${blog?.node?.slug}`}
                                className="text-decoration-none  d-block "
                              >
                                <div
                                  id="arrow_right"
                                  className="jt_blog_img_wrapper"
                                >
                                  <Image
                                    src={siteUrl + blog?.node?.blogImg}
                                    alt={blog?.node?.title}
                                    className="jt_blog_img"
                                  />

                                  <div className="blog_arrow_second">
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="15"
                                      height="18"
                                      viewBox="0 0 15 18"
                                      fill="none"
                                    >
                                      <path
                                        d="M15 9.22363L0 17.8839L0 0.563378L15 9.22363Z"
                                        fill="white"
                                      />
                                    </svg>
                                  </div>
                                </div>
                              </Link>
                            </Col>
                          </Row>
                        </Card>
                      )}
                    </Col>
                  );
                })}
              </Row>
            </Container>
          </div>
        </Fragment>
      )}
      {latestBlogs?.length > 0 && (
        <Fragment>
          <div
            className="blog_details_bg"
            style={{
              background: "transparent",
            }}
          >
            <h2
              style={{
                color: "#7534fc",
              }}
            >
              Latest Blogs
            </h2>
          </div>
          <div
            className="blog_list_bg"
            style={{
              background: "transparent",
            }}
          >
            <Container className="blog_container_position">
              <Row className="blog_row_gap">
                {latestBlogs
                  ?.slice(0, numberItemsPagination)
                  .map((ele, ind) => {
                    const { blog } = ele;
                    let blogId = ind;
                    if (blogId % 2 === 0) {
                      count = count + 1;
                    }
                    return (
                      <Col lg={6} xs={12} className="p-lg-0">
                        {count % 2 !== 0 ? (
                          <Card key={ele.id} className="jt_blog_card">
                            <Row className="mx-lg-0">
                              <Col
                                sm={6}
                                xs={12}
                                className="p-sm-0 d-flex justify-content-center"
                              >
                                <Link
                                  to={`${blog?.node?.slug}`}
                                  className="text-decoration-none  d-block "
                                >
                                  <div
                                    id="arrow_right"
                                    className="jt_blog_img_wrapper"
                                  >
                                    <Image
                                      src={siteUrl + blog?.node?.blogImg}
                                      alt={blog?.node?.title}
                                      className="jt_blog_img"
                                    />
                                    <div className="blog_arrow_first">
                                      <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="15"
                                        height="18"
                                        viewBox="0 0 15 18"
                                        fill="none"
                                      >
                                        <path
                                          d="M0 9L15 0.339745L15 17.6603L0 9Z"
                                          fill="white"
                                        />
                                      </svg>
                                    </div>
                                  </div>
                                </Link>
                              </Col>
                              <Col
                                sm={6}
                                xs={12}
                                className="p-sm-0 d-flex justify-content-center "
                              >
                                <Link
                                  to={`${blog?.node?.slug}`}
                                  className="jt_blog_card_body"
                                >
                                  <p className="jt_blog_date">
                                    {blog?.node?.blog_created_date}
                                  </p>

                                  <p className="jt_blog_title">
                                    {blog?.node?.title}
                                  </p>
                                  <p className="jt_blog_description">
                                    {blog?.node?.short_description.slice(
                                      0,
                                      65
                                    ) + "..."}
                                  </p>
                                </Link>
                              </Col>
                            </Row>
                          </Card>
                        ) : (
                          <Card key={ele.id} className="jt_blog_card">
                            <Row className="mx-lg-0">
                              <Col
                                sm={6}
                                xs={12}
                                className="p-sm-0 d-flex justify-content-center order_two "
                              >
                                <Link
                                  to={`${blog?.node?.slug}`}
                                  className="jt_blog_card_body"
                                >
                                  <p className="jt_blog_date">
                                    {blog?.node?.blog_created_date}
                                  </p>

                                  <p className="jt_blog_title">
                                    {blog?.node?.title}
                                  </p>
                                  <p className="jt_blog_description">
                                    {blog?.node?.short_description.slice(
                                      0,
                                      65
                                    ) + "..."}
                                  </p>
                                </Link>
                              </Col>
                              <Col
                                sm={6}
                                xs={12}
                                className="p-0 d-flex justify-content-center order_one"
                              >
                                <Link
                                  to={`${blog?.node?.slug}`}
                                  className="text-decoration-none  d-block "
                                >
                                  <div
                                    id="arrow_right"
                                    className="jt_blog_img_wrapper"
                                  >
                                    <Image
                                      src={siteUrl + blog?.node?.blogImg}
                                      alt={blog?.node?.title}
                                      className="jt_blog_img"
                                    />

                                    <div className="blog_arrow_second">
                                      <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="15"
                                        height="18"
                                        viewBox="0 0 15 18"
                                        fill="none"
                                      >
                                        <path
                                          d="M15 9.22363L0 17.8839L0 0.563378L15 9.22363Z"
                                          fill="white"
                                        />
                                      </svg>
                                    </div>
                                  </div>
                                </Link>
                              </Col>
                            </Row>
                          </Card>
                        )}
                      </Col>
                    );
                  })}

                {numberItemsPagination !== latestBlogs?.length && (
                  <Col md={12}>
                    <Button
                      disabled={
                        isLoading ||
                        numberItemsPagination >= latestBlogs?.length
                      }
                      className="blog_load_btn"
                      onClick={() => loadMoreItems(numberItemsPagination + 2)}
                    >
                      Load more {isLoading && <Loading />}
                    </Button>
                  </Col>
                )}
              </Row>
            </Container>
          </div>
        </Fragment>
      )}
    </Container>
  );
};

export default BlogsData;
